@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&family=Ubuntu:wght@400;500;700&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
}
/* navbar styling */
.navbar{
    background:#111;
    position: fixed;
    width: 100%;
    z-index: 999;
    /* to hide about me 'z-index' */
    padding: 30px 0;
    
    /* background-color:aquamarine; */
    transition: all 0.3s ease;
}
.navbar .sticky{
    padding: 30px 0;
    background: crimson;
}
.max-width{
    max-width: 1300px;
    padding: 0 80px;
    margin: auto;   
}
.navbar .menu li a{
    
    
    /* color: crimson; */
    
    color: crimson;
    margin-left: 25px;
    font-weight: 500;
    font-size: 18px;
    transition: color 0.3s ease;

}
.navbar  .logo a span{
    color: crimson;
    
}
.navbar .menu li a:hover{
    color: darkblue;
}
.menu{
    color: crimson;
    margin-left: 25px;
    font-weight: 500;
    font-size: 18px;
    transition: color 0.3 ease;
    
}
.navbar .logo a{
    color: darkblue;
    font-size: 35px;
    font-weight: 600;
}
.navbar .menu li{
     list-style: none;
     display: inline-block;
}
.navbar .max-width{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
/* home section styling */
.home{
    display: flex;
    background: url("images/wolf\ wall.jpg") no-repeat center;
    height: 100vh;
    background-size: cover;
    background-attachment: fixed;
    color: #fff;
    min-height: 500px;
    
}
.home .max-width{
    margin: auto 0 auto 40px;
    /* this decides the position of the text */

}
.home .home-content .text-1{
    font-size: 28px;
}
.home .home-content .text-2{
    font-size: 75px;
    font-weight: 600;
    margin-left: -3px;
}
.home .home-content .text-3{
    font-size: 40px;
    margin:5px 0;
    
}
.home .home-content .text-3 span{
    color: darkblue;
    font-weight: 500;
}

/* about section styling */
section{
    padding: 100px 0;
}
.about, .services{
    font-family: 'montserrat';
}
section .title{
    position: relative;
    
    text-align: center;
    font-size: 40px;
    font-weight: 500;
    margin-bottom: 60px;
    padding-bottom: 20px;
}
section .title::before{
    content:"";
    position: absolute;
    bottom: 0px;
    left: 50%;
    width: 180px;
    height: 3px;
    background: #111;
    transform: translate(-50%);
}
/* about me underline */
section .title::after{
    
    position: absolute;
    bottom: -12px;
    left: 50%;
    font-size: 20px;
    color: crimson;
    padding: 5px;

    
    background: #fff;
    transform: translate(-50%);
}
.about .title::after{
    content: "who I am";
}
.about .about-content, .services .services-content{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}
.about .about-content .left{
    width: 45%;
}
.about .about-content .right{
    width: 55%;
}
.about .about-content .right .text{
    font-size: 25px;
    font-weight: 600;
    margin-bottom: 10px;
}
.about .about-content .right .text span{
    color: darkblue;
}
.about .about-content .right p{
    text-align: justify;
}
.about .about-content .right a{
    text-align: justify;
}
.about .about-content .right a{
    display: inline-block;
    background: crimson;
    color: #fff;
    font-size: 25px;
    padding: 12px 36px;
    margin-top: 20px;
    border-radius: 6px;
    border: 2px solid crimson;
    transition: all 0.3s ease;
}
.about .about-content .right a:hover{
    color: crimson;
    background: none;
}
.about .about-content .left img{
    height:400px;
    width:400px;
    object-fit: cover;
    border-radius: 60px;


}
/* sevices section styling */
.services{
    background: #111;
    color: #fff;
}
.services .title{
    color: crimson;
}
/* .services .title::after{
    content: "";
} */
.services .title::before{
    background: darkblue;
}
.services .services-content .card{
    width: calc(33% - 20px);
    background: darkblue;
    text-align: center;
    border-radius: 60px;
    cursor: pointer;
    padding: 40px 45px;
    transition: all 0.3s ease;
}
.services .services-content .card .text{
    font-size: 25px;
    font-weight: 500;
    margin: 10px 0 7px 0;
}
.services .services-content .card .box{
    transition: all 0.3s ease;

}
.services .services-content .card:hover .box{
    transform: scale(1.3);

}
.services .services-content .card:hover{
    background: #fff;
    color: darkblue;
}

/* footer styling */
.footer{
    background: #111;
    /* position: relative; */
    
    text-align: center;
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 0px;
    padding-bottom: 0px;
}
.footer-span1{
    text-align: center;
    color:crimson;
}
.footer-span{
    text-align: center;
    color: darkblue;
}
.about{
  
  margin-bottom: 5%;
}
.about .about-button{
  
  display: flex;
  background-color:crimson; 
  border: none;
  text-decoration-color: #fff;
  justify-content: center;
  padding: 15px 32px;
  display: inline-block;
  margin: 10px;
 
  font-size: 18px;
  border-radius: 12px;
  transition-duration: 0.4s;
}
.about .about-button:hover{
  color:crimson;
  background: #fff;
  border: 2px solid crimson;

}
.about .button-div{
  display: flex;
  justify-content: center;
  align-items: center;
  
  
}
.about .About u{
  text-decoration-color: blue;
}
.about .About{
  text-align: center;
  padding-top: 10%;
  
 
  font-size: 40px;

}
.about .para-about{
  padding-top: 5%;
  padding-bottom: 5%;
  text-align: center;
  font-size: 25px;

}
.footer{
  padding-top: 4%;
  padding-bottom: 5%;
}
.column {
  float: left;
  width: 33.3%;
  padding: 10px;
}
.row {margin: 0 -5px;}
.row:after {
  content: "";
  display: table;
  clear: both;
}
/* Responsive columns */
@media screen and (max-width: 600px) {
  .column {
    width: 100%;
    display: block;
    margin-bottom: 20px;
  }
}
.card{
  padding: 16px;
  text-align: center;
}
* {
  box-sizing: border-box;
}
@media only screen and (max-width: 600px) {
  .card .box .text {
    font-size: 15px;
  }
}
.skills-container{
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.web{
  flex:1;
  margin:10px;
  padding: 2%;
  background-color: darkblue;
  text-align: center;
  border-radius: 12px;

}
.data{
  flex:1;
  margin: 10px;
  text-align: center;
  border-radius: 12px;
  background-color:darkblue;
  padding: 2%;
  
}
.app{
  flex:1;
  margin: 10px;
  text-align: center;
  border-radius: 12px;
  background-color:darkblue;
  padding: 2%;
  
}
@media only screen and (max-width: 500px) {
  .web {
    flex-basis: 100%;
    order: 1;

  }
  .data {
    flex-basis: 100%;
    order: 2;
    
  }
  .app {
    flex-basis: 100%;
    order: 3;
  }
}
